<template lang="html">
  <x-header>
    首页
    <a slot="right" @click="logout" href="javascript:;">
      退出
    </a>
  </x-header>
  <div class="team_logo">
    <div class="logo">
      <img :src="user.avatar" alt="" />
    </div>
  </div>
  <div class="weui_cells">
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-at"></i> 姓名
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{user.name}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-wordpress"></i> 体重
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{user.weight}} 公斤</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-h-square"></i> 身高
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{user.height}} cm</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-tag"></i> 位置
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{user.position}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
  </div>
  <div class="button_sp_area" style="margin-top:20px;padding: 10px;">
    <a v-link="{name: 'usersedit'}" class="weui_btn weui_btn_plain_primary">编辑资料</a>
  </div>
  <div class="button_sp_area" style="padding: 10px;">
    <a v-link="{name: 'teams'}" class="weui_btn weui_btn_plain_primary">加入球队</a>
  </div>
  <toast :show.sync="loadFail" type="text">加载失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'
  import * as userService from '../services/userService'

  export default {
    data: function () {
      return {
        user: {
          avatar: {avatar: {url: ''}}
        },
        loadFail: false
      }
    },
    route: {
      data (transition) {
        let userId = localStorage.getItem('ruzun_user_id') || 0
        userService.getUser(userId)
        .then((responseJson) => {
          this.$set('user', responseJson.user)
        })
        .catch((error) => {
          console.log(error)
          this.$set('loadFail', true)
        })
      }
    },
    methods: {
      logout () {
        localStorage.removeItem('ruzun_user_token')
        localStorage.removeItem('ruzun_user_id')
        this.$route.router.go({name: 'signin'})
      }
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="css">
</style>



